import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Grid from '@mui/material/Grid';
import MainLogo from './Components/foto1.jpg';
import MainLogo2 from './Components/foto2.jpg'
import MainLogo3 from './Components/foto3.jpg'
const cards = [
  { id: 1, title: 'Inventory System', image: MainLogo },
  { id: 2, title: 'Invoice management system', image: MainLogo2 },
  { id: 3, title: 'Shift Management System', image: MainLogo3 },
  // ... otras tarjetas ...
];
export default function Proyectos() {
  return (
    <main>
      <Container sx={{ py: 8 }} maxWidth="md">
        <Grid container spacing={4}>
          {cards.map((card) => (
            <Grid item key={card.id} xs={12} sm={6} md={4}>
              <Card
                sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
              >
                <CardMedia
                  component="img"  // Cambié 'div' a 'img'
                  sx={{
                    // Estilos adicionales si es necesario
                  }}
                  alt={card.title}
                  height="140"  // Cambié 'pt' a 'height' con un valor específico
                  image={card.image}
                />
                <CardContent sx={{ flexGrow: 1 }}>
                  <Typography gutterBottom variant="h5" component="h2">
                    {card.title}
                  </Typography>
                  <Typography>
                    This is a media card. You can use this section to describe the content.
                  </Typography>
                </CardContent>
                <CardActions>
                  <Button size="small">View</Button>
                  <Button size="small">Edit</Button>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </main>
  );
}
